*{
  margin:0;
  box-sizing: border-box;
}
body{
  background-color: #f5f6fa;
}
.app_header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.app{
  display:flex;
  justify-content: space-evenly;
  padding: 20px;
  
}
.app_left{
  flex:0.9;
}

@media (max-width:800px){
  .app{
    flex-direction: column;
  }
}
.app_stats{
  display: flex;
  justify-content: space-between;
}